// import { Fragment } from 'react'

import AccessTimeIcon from '@material-ui/icons/AccessTime'
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline'
import ReportProblemOutlinedIcon from '@material-ui/icons/ReportProblemOutlined'

export default function PendingTx(props) {

  const { tx } = props

  return (
    <div className="PendingTx">
      { tx.status === 'pending' &&
        <AccessTimeIcon fontSize="small" style={{ marginRight: '0.4rem', fill: '#ffb64b' }} />
      }
      { tx.status === 'confirmed' &&
        <CheckCircleOutlineIcon fontSize="small" style={{ marginRight: '0.4rem', fill: '#1de9b6' }} />
      }
      { tx.status === 'failed' &&
        <ReportProblemOutlinedIcon fontSize="small" style={{ marginRight: '0.4rem', fill: '#dc004e' }} />
      }
      <div>
        <h4>
          <a href={`https://etherscan.io/tx/${tx.hash}`} target="_blank" rel="noreferrer">
            { tx.status === 'pending' &&
              <span style={{color: '#ffb64b'}}> Transaction Pending</span>
            }
            { tx.status === 'confirmed' &&
              <span style={{color: '#1de9b6'}}> Transaction Confirmed</span>
            }
            { tx.status === 'failed' &&
              <span style={{color: '#dc004e'}}> Transaction Failed</span>
            }
          </a>
        </h4>
        <h5>
          <a href={`https://etherscan.io/tx/${tx.hash}`} target="_blank" rel="noreferrer">
            <b>Bid {tx.type} &middot; </b>{tx.quantity} NFT{ (tx.quantity > 1) ? 's' : ''} from {tx.projectName} for {tx.bid} ETH each
          </a>
        </h5>
      </div>
    </div>
  )
}
