import { Fragment, useEffect } from 'react'
import { ethers } from 'ethers'
import { Button } from '@material-ui/core'
import ErrorIcon from '@material-ui/icons/Error'

import Spinner from './../Spinner'

export default function Connect(props) {

  const { 
    wallet, 
    setWallet } = props

  const ethereum = window.ethereum

  async function getConnectedWallet() {
    try {
      // Attempt to get the provider from Metamask
      const provider = new ethers.providers.Web3Provider(ethereum)

      try {
        // Test whether the Metamask account has already been connected
        const signer = provider.getSigner()
        const signerAddress = await signer.getAddress()
        // If getAddress doesn't throw an error, then hypothetically the user's Metamask account is connected
        setWallet(prevWallet => {
          return { ...prevWallet, provider, signer, connected: true, signerAddress }
        })
      } catch(err) {
        // error is thrown by getAddress if user is not connnected
        // Error: unknown account #0 (operation="getAddress", code=UNSUPPORTED_OPERATION, version=providers/5.0.17)
        console.log('Your Metamask account is not connected. Please click "Connect Wallet" to connect your Metamask account.')
      }
    } catch(err) {
      // error is thrown if user does not have Metamask installed
      // Error: missing provider (argument="provider", value=undefined, code=INVALID_ARGUMENT, version=providers/5.0.17)
      setWallet(prevWallet => {
        return { ...prevWallet, disabled: true, error: 'You must install the Metamask browser plugin before you can connect a wallet.'}
      })
    }
  }

  useEffect(() => {
    getConnectedWallet()
  }, [])

  const handleConnect = async () => {
    setWallet(prevWallet => {
      return {...prevWallet, pending: true, error: ''}
    })

    try { 
      await ethereum.enable()
      await getConnectedWallet()

      setWallet(prevWallet => {
        return {...prevWallet, pending: false}
      })
    } catch (err) {
      // error is thrown if user rejects connection.
      // {code: 4001, message: "User rejected the request."}
      setWallet(prevWallet => {
        return {...prevWallet, pending: false, error: 'User rejected the request.'}
      })
    }
  }

  return (
    <Fragment>
      <Button variant="contained" color="primary" disableElevation fullWidth onClick={handleConnect} disabled={wallet.disabled} style={{ minWidth: '194px' }}>
        { wallet.pending ? 
            <Spinner />
          :
            'Connect Wallet'
        }
      </Button>
      { wallet.error.length > 0 &&
        <div className="Message--error">
          <ErrorIcon fontSize="small" />
          <p>{ wallet.error }</p>
        </div>
      }
    </Fragment>
  )
}
