// use the `getEventsForUser` function to get eventsForUser 
export async function getUserHistory(eventsForUser) {
	// get all events related to this user
	const historyArray = eventsForUser.map(event => {
		const action = {
			action: event.args._action,
			artBlocksProjectId: event.args._artBlocksProjectId,
			optionalTokenId: event.args._optionalTokenId,
			priceInWeiEach: event.args._priceInWeiEach,
			quantity: event.args._quantity,
			txHash: event.txHash
		}
		return action
	})
	return historyArray
}

// use the `getEventsForUser` function to get eventsForUser 
export async function getHumanReadableUserHistory(eventsForUser) {
	const userHistory = await getUserHistory(eventsForUser)
	const humanReadableUserHistory = userHistory.map(event => {
		return stringifyUserAction(event)
	})
	return humanReadableUserHistory
}

function stringifyUserAction(event) {
	if (event.action == 'order placed') {
		return {
			explanation: `Order placed for projectId: ${event.artBlocksProjectId}. Quantity: ${event.quantity}. Price offered each: ${ethers.utils.formatEther(event.priceInWeiEach)} ETH each.`,
			transactionHash: event.txHash
		}
	} else if (event.action == 'order cancelled') {
		return {
			explanation: `Order cancelled for projectId: ${event.artBlocksProjectId}`,
			transactionHash: event.txHash
		}
	} else if (event.action == 'order fulfilled') {
		if (event.quantity.eq('0')) {
			// order was completely fulfilled
			return {
				explanation: `Order fulfilled for projectId: ${event.artBlocksProjectId}. TokenId: ${event.optionalTokenId}`,
				transactionHash: event.txHash
			}
		} else {
			// order was partially fulfilled
			return {
				explanation: `Order (partially) fulfilled for projectId: ${event.artBlocksProjectId}. TokenId: ${event.optionalTokenId}. There are ${event.quantity} remaining for this order.}`,
				transactionHash: event.txHash
			}
		}
	} else {
		return {
			explanation: `Error: unable to parse event`,
			transactionHash: event.txHash
		}
	}
}

// use the `getEventsForUser` function to get eventsForUser 
export async function getLatestOrdersForUser(user, eventsForUser) {
	// get the set of all AB project Ids this user has ever created an order for
	const projectIds = Array.from(new Set(eventsForUser.map(event => event.args._artBlocksProjectId.toString())))
	// for each project ID, get the user's latest order
	const orders = projectIds.map(projectId => {
		// get all the logs for this user related to the this project ID
		const eventsForThisUserAndProjectId = eventsForUser.filter(event => event.args._artBlocksProjectId == projectId)
		if (eventsForThisUserAndProjectId.length == []) throw new Error(`logsForThisUserAndProjectId is empty when it shouldn't be. user: ${user}, projectId: ${projectId}`)
		// logs are chronological, so just grab the latest one
		const latestEvent = eventsForThisUserAndProjectId[eventsForThisUserAndProjectId.length - 1]
		return {artBlocksProjectId: latestEvent.args._artBlocksProjectId, priceInWeiEach: latestEvent.args._priceInWeiEach, quantity: latestEvent.args._quantity}
	})
	// filter out those orders that have 0 _quantity
	const nonEmptyOrders = orders.filter(order => !order.quantity.eq('0'))

	return nonEmptyOrders
}

// use the `getEventsForProjectId` function to get eventsForProjectId 
function getLatestOrdersForProjectId(eventsForProjectId) {
	const eventsInReversedOrder = eventsForProjectId.reverse()
	// go through all the events (in reverse order), grab the latest user order and store it in a Map
	const ordersMap = new Map()
	eventsInReversedOrder.map(event => {
		if (!ordersMap.has(event.args._user)) {
			ordersMap.set(event.args._user, {user: event.args._user, priceInWeiEach: event.args._priceInWeiEach, quantity: event.args._quantity})
		}
	})
	const ordersArray = Array.from(ordersMap, ([, value]) => value)
	const nonZeroOrders = ordersArray.filter(order => !order.quantity.eq('0'))
	return nonZeroOrders
}

export async function getEventsForUser(user) {
	// get all events related to this user
	const filter = ABBrokerContract.filters.Action(user)
	filter.fromBlock = filterStartBlock
	const logs = await provider.getLogs(filter)
	// console.log(logs)
	const events = logs.map(log => {
		let parsedLog = ABBrokerInterface.parseLog(log)
		parsedLog.txHash = log.transactionHash
		return parsedLog
	})
	return events
}

export async function getEventsForProjectId(projectId) {
	// get all events related to this projectId
	const filter = ABBrokerContract.filters.Action(null, projectId)
	filter.fromBlock = filterStartBlock
	const logs = await provider.getLogs(filter)
	// console.log(logs)
	const events = logs.map(log => {
		let parsedLog = ABBrokerInterface.parseLog(log)
		parsedLog.txHash = log.transactionHash
		return parsedLog
	})
	return events
}

export async function getAllArtBotterEvents() {
	// get all events from the ArtBotter contract
	const filter = ABBrokerContract.filters.Action()
	filter.fromBlock = filterStartBlock
	const logs = await provider.getLogs(filter)
	// console.log(logs)
	const events = logs.map(log => {
		let parsedLog = ABBrokerInterface.parseLog(log)
		parsedLog.txHash = log.transactionHash
		return parsedLog
	})
	return events
}

// returns a Map(projectId => array of orders sorted from highest to lowest price offered)
export async function getSortedOrdersForAllProjects() {
	const allEvents = await getAllArtBotterEvents()
	const projectIds = Array.from(new Set(allEvents.map(event => event.args._artBlocksProjectId.toString())))
	const orders = new Map()
	projectIds.map(projectId => {
		const eventsForProjectId = getEventsForProjectIdFromAllEvents(projectId, allEvents)
		const latestOrders = getLatestOrdersForProjectId(eventsForProjectId)
		// sort latestOrders based on price offered
		const sortedOrders = latestOrders.sort((a,b) => a.gt(b))
		orders.set(projectId, sortedOrders)
	})
	return orders
}

function getEventsForProjectIdFromAllEvents(projectId, allEvents) {
	return allEvents.filter(event => event.args._artBlocksProjectId.eq(projectId))
}
