export async function canMint(projectId) {
	const minterAllowed = await ABFactoryContract.isMintWhitelisted(ABMinterAddress)
	if (!minterAllowed) throw new Error(`Minter contract has been changed. It is no longer ${ABMinterAddress}`)

	const tokenInfo = await getProjectTokenInfo(projectId)
	// must not exceed max invocations on factory contract
	if (tokenInfo.invocations.gte(tokenInfo.maxInvocations)) {
		console.log(`project ${projectId} is sold out`)
		return false;
	} 
	// must be active
	if (!tokenInfo.active) {
		console.log(`project ${projectId} is not active`)
		return false;
	}
	const scriptInfo = await getProjectScriptInfo(projectId)
	// must not be paused
	if (scriptInfo.paused) {
		console.log(`project ${projectId} is paused by artist`)
		return false;
	}
	// check that maxInvocations has been set on the minter contract
	// TODO

	// check that maxMints has been set on the minter contract
	// TODO

	return true
}
